    border-radius: 50px;
    margin-bottom: 20px;
    display: flex;
    font-size: 24px;
    justify-content: flex-end;
    align-items: center;
    color: white;
    background-color: #0a0a23;
    padding: 11px 30px 11px;
    margin: 1px solid #888;
    box-shadow: -1px 4px 1px 1px white;
}