.App{
  height: 100vh;
  padding-top: 20px;
  background-color: #1b1b32;
  display: flex;
  flex-direction: column;
  font-family: Lato, sans-serif;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.App img {
  height: 50px;
}

.calculator {
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: #99c9ff;
  border-radius: 20px;
  border: 5px solid white;
}

.row {
  display: flex;
}